<template>
    <div class="navbar flex-row-between" :class="{'show-bav__bg': bg || showLanguage || drawer}">
        <div class="navbar-left flex-row-start">
            <img src="~@/assets/images/home/logo.png" class="navbar-left__log pointer" />
            <div class="navbar-left__list flex-row-start">
                <div 
                class="navbar-left__item text-center pointer text-white text-18"
                v-for="(item, index) in navList"
                :key="index"
                @click="navClick(item, index)">{{item.name}}</div>
            </div>
        </div>
        <div class="navbar-right flex-row-end">
            <div class="navbar-right__button pointer text-white text-16">
                <a class="text-white" href="https://pancakeswap.finance/swap?outputCurrency=0x9e551b430b7ab145c96c6ede72a34ec2a90d4eca" target="_blank" rel="noopener noreferrer">
                    Buy
                </a>
            </div>
            <div class="navbar-right__button pointer text-white text-16">Dapp</div>
            <el-icon color="#fff" :size="22" class="ML-10 pointer navbar-right__menu" @click="drawer = true"><Menu /></el-icon>
            <div class="navbar-right__language text-white text-16 text-center pointer">
                <span @click="showHandler" class="navbar-right__language-span">Language</span>
                <transition name="el-zoom-in-top">
                    <div class="navbar-language__list text-white text-20" v-show="showLanguage">
                        <div class="navbar-language__item" @click="showLanguage = false">English</div>
                        <div class="navbar-language__item" @click="showLanguage = false">中文</div>
                    </div>
                </transition>
            </div>
        </div>
        <el-drawer
            v-model="drawer"
            direction="ltr"
            :show-close="false"
            append-to-body
            :with-header="false"
            custom-class="nav-drawer"
            modal-class="nav-modal"
        >
            <div 
                class="nav-drawer__item PL-30 pointer text-white text-22"
                v-for="(item, index) in navList"
                :key="index"
                @click="navClick(item, index)">{{item.name}}</div>
            <div class="nav-drawer__item PL-30 pointer text-white text-22" @click="showHandler">Language</div>
            <transition name="el-zoom-in-top">
            <div class="nav-drawer__item-lang PL-30" v-show="showLanguage">
                <div class="nav-lang__item PL-30 pointer text-white text-22">English</div>
                <div class="nav-lang__item PL-30 pointer text-white text-22">中文</div>
            </div>
            </transition>
        </el-drawer>
    </div>
</template>

<script>
import { Menu } from '@element-plus/icons'
export default {
    components: {
        Menu
    },
    props: {
        showNavBg: Boolean
    },
    data() {
        return {
            bg: false,
            showLanguage: false,
            drawer: false,
            navList: [{
                name: 'Trade'
            },{
                name: 'Earn'
            },{
                name: 'Win'
            },{
                name: 'Docs'
            }]
        }
    },
    watch: {
        showNavBg: {
            immediate: true,
            handler(val) {
                this.bg = val
            }
        }
    },
    mounted() {
        
        document.addEventListener('click', this.clickHandler)
    },
    methods: {
        navClick(item, index){
            this.$emit('click', item, index)
            this.drawer = false
        },
        clickHandler(e) {
            if (e.target.className != 'navbar-right__language-span') {
                this.showLanguage = false
            }
        },
        showHandler() {
            this.showLanguage = !this.showLanguage
        }
    },
    beforeDestroy() {
        document.removeEventListener('click', this.clickHandler)
    },
}
</script>

<style lang="scss">
.nav-drawer{
    background: #28282e;
    width: 60% !important;
    top: .9rem !important;
    box-shadow: none;
    .el-drawer__body{
        padding: 0;
    }
}
.nav-modal{
    background: rgba($color: #000000, $alpha: 0);
}
</style>

<style lang="scss" scoped>
@media only screen and (max-width: 768px){
    .navbar-right__language, .navbar-left__list{
        display: none !important;
    }
    .navbar-right__menu{
        display: block !important;
    }
}
.navbar-right__menu{
    display: none;
}

.navbar{
    width: 100%;
    height: .9rem;
    padding: 0 5%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    transition: all .3s;
    z-index: 50;
    &.show-bav__bg{
        background: #28282e;
    }
    .navbar-left__log{
        width: .5rem;
        height: .5rem;
    }
    .navbar-left__item{
        height: .5rem;
        line-height: .5rem;
        width: 1.2rem;
    }
    .navbar-right__button{
        padding: .1rem;
        border-radius: .3rem;
        background: linear-gradient(-68deg, #FF9E00, #BED400);
        margin-right: .1rem;
        min-width: 1rem;
    }
    .navbar-right__language{
        width: 1.2rem;
    }
    .navbar-language__list{
        width: 3rem;
        position: fixed;
        top: .9rem;
        right: 0;
        background: #28282e;
        .navbar-language__item{
            line-height: .8rem;
            &:hover{
                background: #3d3d42;
            }
        }
    }
}
.nav-drawer__item, .nav-lang__item{
    line-height: .8rem;
    &:hover{
        background: #3d3d42;
    }
}
</style>